import Vue from 'vue'
//Vuex
import Vuex from 'vuex'

//
var car=JSON.parse(localStorage.getItem("car"))|| [];

//vuex
Vue.use(Vuex);
//
export default new Vuex.Store({
    state:{//this.$store.state.count
        //data
        //state this.$store.state.count;
        //
        //
        count:0,
        //
        location:{cityCode:"440100",latitude:"23.12463",longitude:"113.36199"},
        car:car//car
        //{id:id,count:count,price:price,selectd:true}
    },
    mutations:{//this.$store.commit('increment')
        //storemutations
        //state
        //mutationsthis.$store.commit('increment') increment:
        //mutationsthis.$emit('')
        //mutations1state2commit
        increment(state){
            state.count++;
        },
        addToCar(state,goodsinfo){
            //some
            var flag=false;
            state.car.some((item)=>{
                if(item.id==goodsinfo.id){
                    item.count += parseInt(goodsinfo.count);
                    flag=true;
                    return true;
                }
            })
            //flagfalsepushpush
            if(!flag){
                state.car.push(goodsinfo);
            }   

            //carcarlocalStorage
            localStorage.setItem('car',JSON.stringify(state.car))         
        },
        //
        undataGoodsInfo(state,goodsInfo){
            state.car.some((item)=>{
                if(item.id==goodsInfo.id){
                    item.count = parseInt(goodsInfo.count);
                    return true;
                }
            })
            //carcarlocalStorage
            localStorage.setItem('car',JSON.stringify(state.car))
        },
        removeFormcar(state,id){
            //idstore
            state.car.some((item,i)=>{
                if(item.id==id){
                   state.car.splice(i,1);
                   return true; 
                }
            })
            //carlocalStorage
            localStorage.setItem('car',JSON.stringify(state.car))
        },
        undataSelected(state,info){
            state.car.some((item,i)=>{
                if(item.id==info.id){
                   item.selected=info.selected
                   return true; 
                }
            })
            //
            localStorage.setItem('car',JSON.stringify(state.car))
        }
    },
    getters:{//this.$store.getters.optCount
        //gettersmutations  count
        // this.$store.getters.optLocation
        optCount:function(state){
            return state.count;
        },
        optLocation:function(state){
            return state.location;
        },
        getAllCount:function(state){
            var c=0;
            state.car.forEach(item=>{
                c+=item.count;
            })
            return c;
        },
        //
        getGoodscount(state){
            var o={};
            state.car.forEach(item=>{
                o[item.id]=item.count
            })
            return o;
        },
        getGoodsSelected(state){
            var j={};
            state.car.forEach(item=>{
                j[item.id]=item.selected
            })
            return j;
        },
        getGoodsCountAndAmount(state){
            var o={
                count:0,//
                amount:0//
            }
            state.car.forEach(item=>{
                if(item.selected){
                    o.count += item.count;
                    o.amount += item.count*item.price;
                }
            })
            return o;
        }
    }
})


