<template>
    <div class="movie_showing">
      <div class="top">
          <h2>{{moviesitem.title}}</h2>
          <a href="/movie/nowintheater?loc_id=108288"></a>
      </div>
      <div class="mainbooks">
        <div class="simg"><img :src="moviesitem.header?moviesitem.header.cover.url:''" alt="" /></div>
        <div>
          <h4><span></span><span>20.0</span></h4>
          <p></p>
        </div>
      </div>
      <div class="bot">        
        <!-- <p>quyu </p> -->
        <div class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
            <div class="mui-scroll">
              <div class="ms-item mui-control-item mui-active" v-for="item in moviesitem.subject_collection_items" @click="routlinkbind(item.id)">
                <img :src="item.cover.url" alt="" />
                <p class="title">{{item.title}}</p>
                <p class="scope">{{item.price?''+item.price:''}}</p>
              </div>
            </div>
        </div>
      </div>
    </div>
</template>
<script type="text/javascript">
// muijs
import mui from '../../lib/mui/js/mui.min.js'
     export default{
        name:'moviesitem',
        data(){
            return{
                
            }
        },
        mounted() {
            //  DOM 
            //  mounted  DOM  
            // 2. 
            mui(".mui-scroll-wrapper").scroll({
                deceleration: 0.0005 //flick 0.0006
            });
        },
        methods:{
            routlinkbind(mId){
                this.$router.push({ name: 'booksinfo', params: { id: mId }})
            },
        },
        props:["moviesitem"]
     }
</script>
<style lang="less" scoped>
    @r:50rem;
*{touch-action:pan-y;}
body{background-color: #fff;}
.mui-segmented-control.mui-scroll-wrapper{
  height:189/@r;
}
.mainbooks{
  margin:6/@r 0;
  display:flex;
  justify-content:space-between;
  .simg{
    img{
      width:100/@r;
      height:130/@r;
    }
  }
  h4{
    width:260/@r;
    span:first-child{
      display:inline-block;
      font-size: 15/@r;width:4rem;    overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
    }
    span:last-child{
      color:#E76648;display:inline-block;
      font-size: 15/@r;
      margin-left: 4/@r;
    }
  }
  p{width:260/@r;font-size: 14/@r;}
}
.movie_showing{
  p{margin-bottom:0;}
  .top{
    display:flex;justify-content:space-between;
    line-height: 30/@r;
    padding: 20/@r 10/@r 0;
    h2{font-size: 15/@r;}
    a{font-size: 14/@r; }
  }
  .bot{
    
    .ms-item{
      text-align: center;
      flex:left;
      margin:0 10/@r;
      padding:0;
      img{
        width:100/@r;
        height:142/@r;
        vertical-align: middle;
      }
      .title{
        font-size: 14/@r;
        width:100/@r;
        height:26/@r;
        line-height: 26/@r;
        overflow:hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        text-align: center;
        color:#333;
      }
      .scope{
        font-size: 12/@r;
        height:22/@r;
        line-height: 22/@r;
        text-align: center;
      }
    } 
    
  }
}
</style>


